import React from 'react'
import styled from 'styled-components'
import { Head } from 'react-static'

// components
import ShareLinks from '../components/ShareLinks'
import PingHeader from '../components/PingHeader'
import { Button } from '../components/Button'
import img from '../assets/apex-ping.jpg'

const PromoPage = styled.div`
  display: block;
  height: 100%;

  .container {
    max-width: 620px;
    margin: 0 auto;
    padding-top: 35px;
    padding-left: 20px;
    padding-right: 20px;
    color: #3B3E48;
  }

  h2 {
    font-size: 20px;
    font-weight: normal;
  }

  ul {
    font-size: 14px;
    line-height: 22px;
    list-style: disc;
    padding-left: 18px;
  }

  div[class*="ShareLinks"] {
    margin-top: 40px;
    margin-bottom: 40px;
  }
`

const Desc = styled.p`
  color: #3B3E48;
  font-size: 16px;
  line-height: 1.6;
  margin-top: 0;
  margin-bottom: 25px;
`

const desc = 'La igualdad de PING es la lucha por otorgar derechos fundamentales a millones de jugadores que actualmente no pueden experimentar sus juegos favoritos con una latencia menor a los 100 ms'
const title = 'Merecemos un mejor PING'

export default () => (
  <PromoPage>
    <Head>
      <title>{title}</title>
      <meta name="description" content={desc} />
      <meta name="keywords" content="FPS, Apex, COD MW2, twitch, jgatjens, League of Legends, Games" />
      <meta name="author" content="jgatjens" />
      <meta property="og:type" content="website" />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={desc} />
      <meta property="og:image" content={img} />
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:site" content="@jgatjens" />
      <meta name="twitter:creator" content="@jgatjens" />
      <meta name="twitter:image" content={img} />
    </Head>
    <PingHeader title={title} />
    <div className="container">
      <Desc>
        La igualdad de PING es la lucha por otorgar derechos fundamentales a millones de
        jugadores que actualmente no pueden experimentar sus juegos favoritos con una latencia
        menor a los 100 ms, Queremos formular una pregunta simple:
      </Desc>

      <h2>
        Por que EA, Steam y Battle.net tienen servers en Sur y Norte America pero
        aqui en America central NO?
      </h2>

      <Desc>
        La comunidad gamer latina alza su voz por esta causa, La desigualdad de PING
        equivale al estancamiento del progreso de nuestra comunidad a nivel competitivo
        y casual. Ayudanos con un click y tu firma para juntos lograr un cambio.
      </Desc>
      <Button href="https://secure.avaaz.org/es/community_petitions/EA_La_igualdad_de_PING" target="_blank">Necesitamos tu ayuda</Button>
      <ShareLinks />
    </div>
  </PromoPage>
)




